<template>
  <svg class="clipbord-svg" data-codes="1" id="i-clipboard"
                v-clipboard='value'
                @click="tooltipChanger"
                v-tooltip="{
                    content:toolbarString,
                    show :tooltipShow,
                    }"
                title="" data-original-title="Copy to clipboard"
                    xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" width="20" height="20"
                    fill="none" stroke="currentcolor" strokeLinecap="round" strokeLinejoin="round"
                    strokeWidth="2">
                    <path d="M12 2 L12 6 20 6 20 2 12 2 Z M11 4 L6 4 6 30 26 30 26 4 21 4" />
                </svg>
            
</template>

<script>
export default {
    // name:"CopyClip",
    props:["value"],
    data(){
  return({
    toolbarString:'Copy to clipboard',
    tooltipShow:false
  })
},
methods:{
 tooltipChanger(){
     let self=this
     setTimeout(function () {
         
        self.toolbarString="Copied";
        //  self.toolbarString='Copy to clipboard',
         self.tooltipShow=true;
         setTimeout(function () {
         
        // self.toolbarString="Copied";
        
         self.tooltipShow=false;
        //  self.toolbarString='Copy to clipboard'
         setTimeout(function () {
         
        // self.toolbarString="Copied";
        
        //  self.tooltipShow=false;
         self.toolbarString='Copy to clipboard'
     },200)
     },500)
     },500)
 }
}
}
</script>

