<template>
  <div class="ratio-parent" v-if="useRaitoSizing">
    <div class="ratio-child">
      <div class="container-fluid w-100 h-100" :style="mainBodyStyle">
        <div
          class="row no-gutters"
          :style="Object.assign(titleStyle, {height:(raitoTitle*10)+'%'})"
        >
          <div class="d-flex justify-content-center align-items-center w-100 h-100">
            <div class="btn btn-small" style="cursor: pointer" @click="perMonth">
              <svg
                id="i-chevron-left"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowLeftColor"
                stroke-width="7.8125%"
              >
                <path d="M20 30 L8 16 20 2" />
              </svg>
            </div>
            <div class="d-flex justify-content-around" style="min-width:20%">
              <div>{{currYear | convertEnToFaDig}} -</div>

              <transition :name="monthAnim">
                <div v-if="changeAnim">{{currMonth}}</div>
              </transition>
            </div>
            <div class="btn btn-small danger" style="cursor: pointer" @click="nextMonth">
              <svg
                id="i-chevron-right"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowRightColor"
                stroke-width="7.8125%"
              >
                <path d="M12 30 L24 16 12 2" />
              </svg>
            </div>
          </div>
        </div>
        <div
          class="row no-gutters flex-nowrap flex-row-reverese"
          :style="Object.assign(headerCalenderStyle,{height:(raitoTableHedear*10)+'%'})"
        >
          <div
            class="col d-flex justify-content-center align-items-center h-100"
            :style="headerCalenderItemStyle"
            v-for="(k) in header"
            :key="k+100+100+';'"
          >{{k}}</div>
        </div>

        <div
          class="row no-gutters flex-nowrap"
          v-for="j in 5"
          :key="'jj'+j"
          :style="Object.assign(rowCalenderStyle,{height:((raitoBody/5)*10)+'%'})"
        >
          <div class="col position-relative" v-for="i in 7" :key="'h'+i">
            <transition :name="calenderItemAnim">
              <div
                @click="selecValues((i-startDay),$event)"
                class="ratio-child"
                :style="Object.assign(styleCellCal1((i-startDay)),calenderCellWithTextStyle)"
                v-if="j==1 && i-1 >=startDay"
              >
                <div
                  class="d-flex justify-content-center align-items-center h-100 w-100"
                >{{i-startDay  | convertEnToFaDig}}</div>
              </div>
              <div v-else-if="j==1" :style="calenderCellNoTextStyle"></div>
              <div
                @click="selecValues((((j-1)*7)+i-startDay),$event)"
                class="ratio-child"
                :style="Object.assign(styleCellCal1((((j-1)*7)+i-startDay)),calenderCellWithTextStyle)"
                v-else-if="j>1 && ((j-1)*7)+i-startDay <=endDay"
              >
                <div
                  class="d-flex justify-content-center align-items-center h-100 w-100"
                >{{((j-1)*7)+i-startDay  | convertEnToFaDig}}</div>
              </div>
              <div v-else :style="calenderCellNoTextStyle"></div>
            </transition>
          </div>
        </div>
        <div
          v-if="showFooter"
          class="row no-gutters"
          :style="Object.assign(footerStyle,{height:(raitoFooter*10)+'%'})"
        >
          <div class="d-flex justify-content-center w-100">
            <div
              v-if="showButtonToday"
              class="btn btn-small"
              :style="Object.assign(todayButtonStyle,{cursor: 'pointer'})"
              @click="goToday"
            >{{todayButtonTitle}}</div>
            <div v-if="showSelectedValue" :style="showSelectedValueStyle">{{inputValue}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>

      <div class="container-fluid" :style="mainBodyStyle" v-else>
        <div
          class="row no-gutters"
          :style="titleStyle"
        >
          <div class="d-flex justify-content-center align-items-center w-100 h-100">
            <div class="btn btn-small" style="cursor: pointer" @click="perMonth">
              <svg
                id="i-chevron-left"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowLeftColor"
                stroke-width="7.8125%"
              >
                <path d="M20 30 L8 16 20 2" />
              </svg>
            </div>
            <div class="d-flex justify-content-around" style="min-width:20%">
              <div>{{currYear}} -</div>

              <transition :name="monthAnim">
                <div v-if="changeAnim">{{currMonth}}</div>
              </transition>
            </div>
            <div class="btn btn-small danger" style="cursor: pointer" @click="nextMonth">
              <svg
                id="i-chevron-right"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 32 32"
                width="18"
                height="16"
                fill="none"
                stroke="currentcolor"
                stroke-linecap="round"
                stroke-linejoin="round"
                :style="arrowRightColor"
                stroke-width="7.8125%"
              >
                <path d="M12 30 L24 16 12 2" />
              </svg>
            </div>
          </div>
        </div>
        <div
          class="row no-gutters flex-nowrap flex-row-reverese"
          :style="headerCalenderStyle"
        >
          <div
            class="col d-flex justify-content-center align-items-center h-100"
            :style="headerCalenderItemStyle"
            v-for="(k) in header"
            :key="k+100+100+';'"
          >{{k}}</div>
        </div>

        <div
          class="row no-gutters flex-nowrap"
          v-for="j in 5"
          :key="'jj'+j"
          :style="rowCalenderStyle"
        >
          <div class="col position-relative" v-for="i in 7" :key="'h'+i">
            <transition :name="calenderItemAnim">
              <div
                @click="selecValues((i-startDay),$event)"
                class="ratio-child"
                :style="Object.assign(styleCellCal1((i-startDay)),calenderCellWithTextStyle)"
                v-if="j==1 && i-1 >=startDay"
              >
                <div
                  class="d-flex justify-content-center align-items-center h-100 w-100"
                >{{i-startDay}}</div>
              </div>
              <div v-else-if="j==1" :style="calenderCellNoTextStyle"></div>
              <div
                @click="selecValues((((j-1)*7)+i-startDay),$event)"
                class="ratio-child"
                :style="Object.assign(styleCellCal1((((j-1)*7)+i-startDay)),calenderCellWithTextStyle)"
                v-else-if="j>1 && ((j-1)*7)+i-startDay <=endDay"
              >
                <div
                  class="d-flex justify-content-center align-items-center h-100 w-100"
                >{{((j-1)*7)+i-startDay}}</div>
              </div>
              <div v-else :style="calenderCellNoTextStyle"></div>
            </transition>
          </div>
        </div>
        <div
          v-if="showFooter"
          class="row no-gutters"
          :style="footerStyle"
        >
          <div class="d-flex justify-content-center w-100">
            <div
              v-if="showButtonToday"
              class="btn btn-small"
              :style="Object.assign(todayButtonStyle,{cursor: 'pointer'})"
              @click="goToday"
            >{{todayButtonTitle}}</div>
            <div v-if="showSelectedValue" :style="showSelectedValueStyle">{{inputValue}}</div>
          </div>
        </div>
      </div>
</template>

<script>
import moment from "moment-jalaali";

import fa from "moment/src/locale/fa";
export default {
  filters:{
    'convertEnToFaDig':function (val) {
  let text=val+''
  if (text.length == 0) {
      return "";
  }
  var ns="۰۱۲۳۴۵۶۷۸۹"
  let out = "";
  let length = text.length;
  for (let i = 0; i < length; i++) {
      let c = text.charAt(i);
      let nump=parseInt(c)
      if(nump>=0){
          out+=ns.charAt(nump)
      }else{
          out+=c
      }
  }
  return out
  
}
  },
  props: {
    
    value:{
      type:String,
      default:""
    },
    todayButtonTitle:{
      type:String,
      default:"امروز"
    },
    calenderItemAnim:{
      type: String,
      default: 'slide-fade'
    },
    monthAnim:{
      type: String,
      default: 'fade'
    },
    selectedClassName: {
      type: String,
      default: "selected"
    },
    todayStyle: {
      type: Object,
      default() {
        return {
          color: "#e84545",
          background: "#00adb5",
          
          boxShadow:"0 0 5px #e84545",
          transition: " all 1s"
        };
      }
    },
    afterTodayStyle: {
      type: Object,
      default() {
        return {
          color: "#eeeeee",
          background: "#3d84a8",
          
          boxShadow:"inset 0 0 10px #48466d",
          transition: "all 1s"
        };
      }
    },
    beforeTodayStyle: {
      type: Object,
      default() {
        return {
          background: "#abedd8",
          boxShadow:"inset 0 0 10px #48466d",
          color: "#48466d",
          transition: "all 1s"
        };
      }
    },
    selectStyle: {
      type: Object,
      default() {
        return {
          background: "orangered",

          transition: "all 1s"
        };
      }
    },
    calenderCellBodyStyle: {
      type: Object,
      default() {
        return {
          // background: "#000",
          // color: "#fff",
          // margin: "auto"
        };
      }
    },
    calenderCellWithTextStyle: {
      type: Object,
      default() {
        return {
          borderRadius: "50%",
          userSelect: "none",
          margin: "2px",
          "-webkit-user-select": "none",
          "-moz-user-select": "none",
          "-ms-user-select": "none",
          cursor: "pointer",

          transition: "all 1s"
        };
      }
    },
    calenderCellNoTextStyle: {
      type: Object,
      default() {
        return {
          background: "#000",
          color: "#fff",
          margin: "auto"
        };
      }
    },
    rowCalenderItemStyle: {
      type: Object,
      default() {
        return {
          // background: "#000",
          // color: "#fff",
          // margin: "auto"
        };
      }
    },
    rowCalenderStyle: {
      type: Object,
      default() {
        return {
          
          margin:"0 -14px"
          // borderBottom:"0.5px solid #eeeeee"
        };
      }
    },
    headerCalenderItemStyle: {
      type: Object,
      default() {
        return {
          color: "#48466d",
          margin: "auto"
        };
      }
    },
    mainBodyStyle: {
      type: Object,
      default() {
        return {
          // background: "#393e46",
          
          background: "#46cdcf",
          // margin: "auto",
          border: "2px solid #eeeeee",
          // border: "2px solid #eee",
          borderRadius: "15px",
          padding: "15px"
        };
      }
    },
    arrowRightColor: {
      type: Object,
      default() {
        return {
          color: "#48466d"
        };
      }
    },

    arrowLeftColor: {
      type: Object,
      default() {
        return {
          color: "#48466d"
        };
      }
    },
    titleStyle: {
      type: Object,
      default() {
        return {
          marginTop: "-10px",
          background: "#46cdcf",
          color: "#48466d"
        };
      }
    },
    headerCalenderStyle: {
      type: Object,
      default() {
        return {
          background: "#abedd8",
          margin:"0 -14px"
        };
      }
    },
    footerStyle: {
      type: Object,
      default() {
        return {
          marginTop:"14px"
          // background: "#222831"
        };
      }
    },
    todayButtonStyle: {
      type: Object,
      default() {
        return {
          color: "#222831",
         outline: "none !important"
          // background: "#222831"
        };
      }
    },
    showSelectedValueStyle: {
      type: Object,
      default() {
        return {
          color: "#222831",
          display: "flex",
          alignItems: "center"
          // background: "#222831"
        };
      }
    },
    customFormat: {
      type: String,
      default: "jYYYY/jM/jD"
    },
    useRaitoSizing: {
      type: Boolean,
      default: true
    },
    raitoTitle: {
      type: Number,
      default: 1
    },
    raitoTableHedear: {
      type: Number,
      default: 1
    },
    raitoBody: {
      type: Number,
      default: 7
    },
    raitoFooter: {
      type: Number,
      default: 1
    },
    showFooter: {
      type: Boolean,
      default: true
    },
    showButtonToday: {
      type: Boolean,
      default: true
    },
    showSelectedValue: {
      type: Boolean,
      default: true
    }
  },
  mounted() {
    // //console.log(this.value)
    if(typeof this.value!=='undefined'&&this.value.length>0){
      
    // //console.log('mounted')
     // let ddd=moment('1360/5/26', 'jYYYY/jM/jD')
    let text=this.value
  var ns="۰۱۲۳۴۵۶۷۸۹"
  let out = "";
  let length = text.length;
  for (let i = 0; i < length; i++) {
      let c = ns.indexOf(text.charAt(i));
      let nump=parseInt(c)
      if(nump>=0){
          out+=c
      }else{
          out+=text.charAt(i)
      }
  }
    ////console.log("object")
    
  ////console.log(out)
  let ddd=moment(out, 'jYYYY/jM/jD')
  ////console.log(ddd.format('jYYYY') )
  ////console.log(text)
  
        this.selectedMonth =  this.monthHeader[
        this.conv(ddd.format("jM"))
      ];
        
        // ddd.format("jM");
        this.selectedYear = ddd.format("jYYYY");
        
        this.selectedDay = ddd.format("jD")
        ////console.log(this.selectedMonth)
        ////console.log(this.selectedYear)
        ////console.log(ddd.format("jD"))
      //   if (this.value != null&& this.value.length>0) {
      //   this.lastSelectedNode.classList.remove(this.selectedClassName);
      //   if (
      //     this.selectedYear == this.momentObject.format("jYYYY") &&
      //     this.selectedMonth == this.momentObject.format("jM")
      //   ) {
      //     this.lastSelectedNode.classList.add(this.selectedClassName);
      //   } else {
      //     this.lastSelectedNode.classList.remove(this.selectedClassName);
      //   }
      // }
    // ////console.log(ddd.jYear())
    // this.ddd.locale("fa", fa);
    // ////console.log(ddd)
    ////console.log('object')
    }
    this.momentObject = moment();
    // let moments = moment();
    //////console.log(this.momentObject);
    this.momentObject.locale("fa", fa);
    this.today = this.conv(this.momentObject.format("YYYY-M-D"));
    this.defFormat = this.momentObject.format("jYYYY/jM/");
    this.todayDay = parseInt(this.conv(this.momentObject.format("jD")));
    this.todayMonth = this.monthHeader[
      this.conv(this.momentObject.format("jM"))
    ];
    this.todayYear = this.momentObject.format("jYYYY");
    // moments.add(2, "jMonth");
    this.momentObject.startOf("jMonth");
    //////console.log(this.momentObject.format("jYYYY/jM/jD"));
    this.startDayMoment = this.momentObject;
    //////console.log(this.startDayMoment.format("jYYYY/jM/jD"));
    let a = "۰۱۲۳۴۵۶۷۸۹";
    this.startDay = parseInt(a.indexOf(this.momentObject.format("e")));

    this.momentObject.endOf("jMonth");
    this.endDay = parseInt(this.conv(this.momentObject.format("jD")));
    this.currMonth = this.monthHeader[
      this.conv(this.momentObject.format("jM"))
    ];
    this.currYear = this.momentObject.format("jYYYY");
    // //////console.log(this.conv(moments.format("jD")));
    // //////console.log(this.startDay);
    // //////console.log(this.endDay);
    // // moments
    // //////console.log(moments.format("jYYYY/jM/jD dd dddd e"));
    // //////console.log(moments.startOf("jMonth").format("jYYYY/jM/jD"));
    // //////console.log(moments.endOf("jMonth").format("jYYYY/jM/jD HH:mm:ss"));
    // moments.add(5, "jMonth");
    // //////console.log(moments.format("jYYYY/jM/jD"));
    // //////console.log(moments.endOf("jMonth").format("jYYYY/jM/jD jHH:mm:ssD"));
  },
  data() {
    return {
      header: {
        0: "ش",
        1: "ی",
        2: "د",
        3: "س",
        4: "چ",
        5: "پ",
        6: "ج"
      },
      monthHeader: {
        1: "فروردین",
        2: "اردیبهشت",
        3: "خرداد",
        4: "تیر",
        5: "مرداد",
        6: "شهریور",
        7: "مهر",
        8: "آبان",
        9: "آذر",
        10: "دی",
        11: "بهمن",
        12: "اسفند"
      },
      defFormat: "jYYYY-jM-",
      startDayMoment: null,
      changeAnim: true,
      currYear: "",
      today: "",
      todayDay: 1,
      todayMonth: "",
      todayYear: "",
      selectedYear: "",
      selectedMonth: "",
      selectedDay: "",
      currMonth: "",
      startDayisBeforeToday: false,
      momentObject: null,
      startDay: 1,
      endDay: 29,
      sDay: 0,
      inputValue: "",
      lastSelectedNode: null,
      lastSelectedNodeClass: ""
    };
  },
  watch: {
    changeAnim() {}
  },
  computed: {
    // setDater(){
    //     //////console.log(this.sDay)
    //     this.sDay=this.sDay+1
    //     return this.sDay
    // },
  },
  methods: {
    styleCellCal1(index) {
      // ////console.log(e+" s ")
      //////console.log(this.currYear, " ", this.todayYear);
      ////console.log(this.selectedDay,"jjjjjjjjj",index," ",this.selectedDay == index,this.currMonth == this.selectedMonth,this.selectedYear == this.conv(this.currYear),this.currYear,this.selectedYear)
      if (
        this.selectedYear == this.conv(this.currYear) &&
        this.currMonth == this.selectedMonth &&
        this.selectedDay == index
      ){
        
      ////console.log(this.selectedDay,"mmmmmmmmmmmmmmmmmmmmmmm")
        return this.selectStyle
      }else{
        
      if (
        this.currYear == this.todayYear &&
        this.currMonth == this.todayMonth
      ) {
        if (this.todayDay == index) {
          return this.todayStyle;
        } else if (this.todayDay >= index) {
          return this.beforeTodayStyle;
        } else {
          return this.afterTodayStyle;
        }
      } else if (this.startDayisBeforeToday) {
        return this.beforeTodayStyle;
      } else {
        return this.afterTodayStyle;
      }
      }
      // if(this.currYear==this.todayYear){
      // }else{
      // }
    },
    styleCellCal(index) {
      //////console.log(this.currYear, " ", this.todayYear);
      if (
        this.currYear == this.todayYear &&
        this.currMonth == this.todayMonth
      ) {
        if (this.todayDay == index) {
          return "ratio-parent base today";
        } else if (this.todayDay >= index) {
          return "ratio-parent base before-today";
        } else {
          return "ratio-parent base after-today";
        }
      } else if (this.startDayisBeforeToday) {
        return "ratio-parent base before-today";
      } else {
        return "ratio-parent base after-today";
      }
      // if(this.currYear==this.todayYear){
      // }else{
      // }
    },
    nextMonth() {
      //////console.log("next");
      this.changeAnim = false;
      let self = this;
      setTimeout(function() {
        self.changeAnim = true;
      }, 500);

      this.momentObject.add(1, "jMonth");
      if (this.lastSelectedNode != null) {
        this.lastSelectedNode.classList.remove(this.selectedClassName);
        if (
          this.selectedYear == this.momentObject.format("jYYYY") &&
          this.selectedMonth == this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ]
        ) {
          this.lastSelectedNode.classList.add(this.selectedClassName);
        } else {
          this.lastSelectedNode.classList.remove(this.selectedClassName);
        }
      }
      this.startDayisBeforeToday = moment(this.today).isAfter(
        this.conv(this.momentObject.format("YYYY/M/D"))
      );
      // ////console.log(
      //   moment(this.today).isAfter(
      //     this.conv(this.momentObject.format("YYYY/M/D"))
      //   )
      // );
      // moments.add(2, "jMonth");
      this.momentObject.startOf("jMonth");
      this.startDayMoment = this.momentObject;
      let a = "۰۱۲۳۴۵۶۷۸۹";
      this.startDay = parseInt(a.indexOf(this.momentObject.format("e")));

      this.momentObject.endOf("jMonth");
      this.endDay = parseInt(this.conv(this.momentObject.format("jD")));
      this.currMonth = this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ];

      this.currYear = this.momentObject.format("jYYYY");
    },
    perMonth() {
      //////console.log("perv");

      this.changeAnim = false;
      let self = this;
      setTimeout(function() {
        self.changeAnim = true;
      }, 500);
      this.momentObject.add(-1, "jMonth");
      if (this.lastSelectedNode != null) {
        this.lastSelectedNode.classList.remove(this.selectedClassName);
        if (
          this.selectedYear == this.momentObject.format("jYYYY") &&
          this.selectedMonth == this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ]
        ) {
          this.lastSelectedNode.classList.add(this.selectedClassName);
        } else {
          this.lastSelectedNode.classList.remove(this.selectedClassName);
        }
      }
      this.startDayisBeforeToday = moment(this.today).isAfter(
        this.conv(this.momentObject.format("YYYY/M/D"))
      );
      // ////console.log(
      //   moment(this.today).isAfter(
      //     this.conv(this.momentObject.format("YYYY/M/D"))
      //   )
      // );
      // moments.add(2, "jMonth");
      this.momentObject.startOf("jMonth");
      this.startDayMoment = this.momentObject;
      let a = "۰۱۲۳۴۵۶۷۸۹";
      this.startDay = parseInt(a.indexOf(this.momentObject.format("e")));

      this.momentObject.endOf("jMonth");
      this.endDay = parseInt(this.conv(this.momentObject.format("jD")));
      this.currMonth = this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ];

      this.currYear = this.momentObject.format("jYYYY");
    },
    selecValues(index, e) {
      if (this.lastSelectedNode == null) {
        this.lastSelectedNode = e.target;
        this.lastSelectedNode.classList.add(this.selectedClassName);
        //////console.log(this.customFormat," s");
        // this.startDayMoment.startOf("jMonth").add(index - 1, "day");
        this.inputValue = this.startDayMoment
          .startOf("jMonth")
          .add(index - 1, "day")
          .format(this.customFormat);
        this.selectedMonth =  this.monthHeader[
        this.conv(this.startDayMoment.format("jM"))
      ];
        
        
        // this.startDayMoment.format("jM");
        this.selectedYear = this.startDayMoment.format("jYYYY");
        this.selectedDay = this.startDayMoment.format("jD")
        // this.startDayMoment.add((-1*index),'day')
        ////////console.log(index);
        ////////console.log(this.lastSelectedNode);
      } else {
        this.lastSelectedNode.classList.remove(this.selectedClassName);
        this.lastSelectedNode = e.target;
        this.lastSelectedNode.classList.add(this.selectedClassName);
        this.inputValue = this.startDayMoment
          .startOf("jMonth")
          .add(index - 1, "day")
          .format(this.customFormat);

        // this.selectedMonth = this.startDayMoment.format("jM");
        this.selectedMonth =  this.monthHeader[
        this.conv(this.startDayMoment.format("jM"))
      ];
        this.selectedYear = this.startDayMoment.format("jYYYY");

        this.selectedDay = this.startDayMoment.format("jD")
        // this.startDayMoment.add((-1*index),'day')
        ////////console.log(index);
        ////////console.log(this.lastSelectedNode);
      }
      this.$emit("input", this.inputValue);

      // e.target
    },
    goToday() {
      this.changeAnim = false;
      let self = this;
      setTimeout(function() {
        self.changeAnim = true;
      }, 500);
      this.momentObject = moment();
      // let moments = moment();
      ////////console.log(this.momentObject);
      this.momentObject.locale("fa", fa);

      // moments.add(2, "jMonth");
      this.momentObject.startOf("jMonth");
      if (this.lastSelectedNode != null) {
        this.lastSelectedNode.classList.remove(this.selectedClassName);
        if (
          this.selectedYear == this.momentObject.format("jYYYY") &&
          this.selectedMonth == this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ]
        ) {
          this.lastSelectedNode.classList.add(this.selectedClassName);
        } else {
          this.lastSelectedNode.classList.remove(this.selectedClassName);
        }
      }
      this.startDayMoment = this.momentObject;
      let a = "۰۱۲۳۴۵۶۷۸۹";
      this.startDay = parseInt(a.indexOf(this.momentObject.format("e")));

      this.momentObject.endOf("jMonth");
      this.endDay = parseInt(this.conv(this.momentObject.format("jD")));
      this.currMonth = this.monthHeader[
        this.conv(this.momentObject.format("jM"))
      ];
      this.currYear = this.momentObject.format("jYYYY");
    },

    setDater() {
      //////console.log(this.sDay);
      this.sDay = this.sDay + 1;
      return this.sDay;
    },
    conv(val) {
      let text = val + "";
      if (text.length == 0) {
        return "";
      }
      var ns = "۰۱۲۳۴۵۶۷۸۹";
      let out = "";
      let length = text.length;
      for (let i = 0; i < length; i++) {
        let c = text.charAt(i);
        let nump = ns.indexOf(c + "");
        if (nump >= 0) {
          out += nump;
        } else {
          out += c;
        }
      }
      return out;
    }
  }
};
</script>

