import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        counter:0
    },
    mutations:{
        increment(state){
            state.counter++
        },
        decrement(state){
            state.counter--
        },
        incrementBy:(state,sum)=>{
            state.counter+=sum
        },
        decrementBy:(state,sum)=>{
            state.counter-=sum
        },
        replace(state,sum){
            state.counter=sum
        },
        clear(state){
            state.counter=0
        }
    },
    actions:{

    },
    getters:{

    }
})
