export default [
    {
        props:"isFiveRow",
        type:"Boolean",
        defualt:"false",
        description:`
        Max row 5 or 6
        `
    },
    {
        props:"todayStyle",
        type:"Object",
        defualt:`
        {
            color: "#e84545",
            background: "#00adb5",
            boxShadow:"0 0 5px #e84545",
            transition: " all 1s"
        }
        `,
        description:`
        Style for today
        `
    },
    {
        props:"afterTodayStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee",
            boxShadow:"inset 0 0 10px #eeeeee",
            background: "#222831",
            transition: "all 1s"
        }
        `,
        description:`
        Style for days after today
        `
    },
    {
        props:"beforeTodayStyle",
        type:"Object",
        defualt:`
        {
            background: "#eeeeee",
            boxShadow:"inset 0 0 10px #222831",
            color: "#222831",
            transition: "all 1s"
        }
        `,
        description:`
        Style for days before today
        `
    },
    {
        props:"selectStyle",
        type:"Object",
        defualt:`
        {
            background: "orangered",
            transition: "all 1s"
        }
        `,
        description:`
        Style for Selected day - require(selectedClassName)
        `
    },
    {
        props:"selectedClassName",
        type:"String",
        defualt:`
        "selected"
        `,
        description:`
        Style for Selected day - require(selectStyle)
        `
    },
    {
        props:"calenderCellWithTextStyle",
        type:"Object",
        defualt:`
        {
            borderRadius: "50%",
            userSelect: "none",
            margin: "2px",
            "-webkit-user-select": "none",
            "-moz-user-select": "none",
            "-ms-user-select": "none",
            cursor: "pointer",
            transition: "all 1s"
        }
        `,
        description:`
        Style for All Day - (1-31)
        `
    },
    {
        props:"calenderCellNoTextStyle",
        type:"Object",
        defualt:`{}`,
        description:`
        Style for Empty cell
        `
    }, 
    {
        props:"rowCalenderStyle",
        type:"Object",
        defualt:`
        {
            margin:"0 -14px"
        }`,
        description:`
        Style for each 5 or 6 row
        `
    },
    {
        props:"headerCalenderStyle",
        type:"Object",
        defualt:`
        {
            background: "#222831",
            margin:"0 -14px"
        }`,
        description:`
        Style for parent of "ش","ی","د","س", "چ","پ","ج"
        `
    }, 
    {
        props:"headerCalenderItemStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee",
            margin: "auto"
        }`,
        description:`
        Style for each "ش","ی","د","س", "چ","پ","ج"
        `
    },
 {
        props:"mainBodyStyle",
        type:"Object",
        defualt:`
        {
            background: "#393e46",
            border: "2px solid #eee",
            borderRadius: "15px",
            padding: "15px"
        }`,
        description:`
        Style for Main body
        `
    },
{
        props:"arrowLeftStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee"
        }`,
        description:`
        Style for left arrow button
        `
    },
{
        props:"arrowRightStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee"
        }`,
        description:`
        Style for right arrow button
        `
    },
    {
            props:"titleStyle",
            type:"Object",
            defualt:`
            {
                marginTop: "-10px",
                background: "#393e46",
                color: "#eeeeee"
            }`,
            description:`
            Style for title(parent of right arrow button and left arrow button)
            `
        }, 
    {
        props:"footerStyle",
        type:"Object",
        defualt:`
        {
            marginTop:"14px"
        }`,
        description:`
        Style for footer
        `
    },
    {
        props:"todayButtonStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee"
        }`,
        description:`
        Style for today Button in footer
        `
    }, 
    {
        props:"showSelectedValueStyle",
        type:"Object",
        defualt:`
        {
            color: "#eeeeee",
            display: "flex",
            alignItems: "center"
        }`,
        description:`
        Style for selected value in footer
        `
    },
    {
        props:"useRaitoSizing",
        type:"Boolean",
        defualt:`true`,
        description:`
        Use percentage height
        `
    }, 
    {
        props:"raitoTitle",
        type:"Number",
        defualt:`1`,
        description:`
        Percentage for title - x of 10 
        `
    }, 
    {
        props:"raitoTableHedear",
        type:"Number",
        defualt:`1`,
        description:`
        Percentage for hedear - x of 10
        `
    }, 
    {
        props:"raitoBody",
        type:"Number",
        defualt:`7`,
        description:`
        Percentage for row (5 or 6 row) - x of 10
        `
    }, 
    {
        props:"raitoFooter",
        type:"Number",
        defualt:`1`,
        description:`
        Percentage for footer (5 or 6 row) - x of 10
        `
    },
    {
        props:"showFooter",
        type:"Boolean",
        defualt:`true`,
        description:`
        Show Footer?
        `
    },
    {
        props:"showButtonToday",
        type:"Boolean",
        defualt:`true`,
        description:`
        Show today button in footer?
        `
    },
{
        props:"showSelectedValue",
        type:"Boolean",
        defualt:`true`,
        description:`
        Show selected value in footer?
        `
    },
    {
        props:"todayButtonTitle",
        type:"String",
        defualt:`"امروز"`,
        description:`
        Title of show Button
        `
    },
]
